<template>
    <div class="layout">
        <img width="180" :src="imgUrl + 'cdn/system/loading.gif'"/>
    </div>
</template>
<script>
    import * as api from "@/api";
    import * as setting from '../../public/config.js';

    export default {
        name: "LoginApp",
        data() {
            return {
                imgUrl: '',
                setting: setting,
            };
        },
        created() {
            if (process.env.NODE_ENV == "development") {
                this.imgUrl = "https://open.woyaou.com/";
            } else {
                this.imgUrl = "https://" + document.domain + "/";
            }
            this.loginApp();
        },
        methods: {
            loginApp() {
                api.post('web/toapp/loginApp', {
                    appid: this.$route.params.id,
                    token: this.$route.params.token,
                }).then(res => {
                    var that = this;
                    this.$store.dispatch('setAppInfo', res).then(() => {
                        setTimeout(function () {
                            that.$router.replace("/dashboard/index");
                        }, 1000);
                    });
                }).catch(() => {

                });
            },
        },

    }
</script>
<style scoped>
    .layout {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>
